* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  box-shadow: 0 0 0 transparent;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  position: relative;
  font-size: 1.125rem;
}

[disabled="disabled"] {
  cursor: default !important;
}

b,
strong,
.strong {
  font-weight: 600;
}

.stronger {
  font-weight: 700;
}

h1,
.h1 {
  color: #232323;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
}

.lead {
  display: block;
  font-size: 1.125rem;
  text-transform: none;
}

ul:not([class]),
ol:not([class]) {
  display: block;
  margin-left: 55px;
}

.btn-primary {
  font-weight: 700;
  background-image: linear-gradient(225deg, #e61225 0%, #ff1f34 100%);
}

.form-group .form-check {
  margin-bottom: 15px;
}

.form-control {
  background-color: #fdfdfd;
}

::placeholder {
  color: #dbdbdb;
  font-size: 15px;
}

input.custom-control-input ~ label {
  margin-left: 26px;
}

input.custom-control-input ~ label::before {
  content: '';
  position: absolute;
  top: 4px;
  left: 1.25rem;
  display: inline-block;
  width: 16px;
  height: 16px;
  background-color: #f8f9fa;
}

input.custom-control-input ~ label::after {
  content: '';
  position: absolute;
  top: 6px;
  left: calc(1.25rem + 2px);
  display: inline-block;
  width: 10px;
  height: 10px;
}

input.custom-control-input[type=radio] ~ label::before,
input.custom-control-input[type=radio] ~ label::after {
  border-radius: 50%;
}

input.custom-control-input:checked ~ label::after {
  background-color: #f00d22;
}

input.custom-control-input:disabled ~ label {
  color: #ced4da;
}

.blue .colorize-bg {
  background-color: #0274ff !important;
}

.blue .colorize-text {
  color: #0274ff !important;
}

.full-w,
.full-h {
  position: relative;
  overflow: hidden;
}

.full-w.bordered,
.full-h.bordered {
  border: 18px solid #030303;
  border-bottom: none;
  border-radius: 8px;
  background-color: #030303;
}

.full-w {
  width: 100%;
}

.full-w img {
  display: inline-block;
  width: 100%;
  height: auto;
}

.full-h {
  height: 100%;
}

.full-h img {
  display: inline-block;
  width: auto;
  height: 100%;
}

.right-side .full-w,
.right-side .full-h,
.left-side .full-w,
.left-side .full-h {
  height: 500px;
}

.right-side .full-w img,
.right-side .full-h img,
.left-side .full-w img,
.left-side .full-h img {
  position: absolute;
  top: 0;
  left: 0;
}

.right-side .full-w img,
.right-side .full-h img {
  left: auto;
  right: 0;
}

.logo svg {
  width: auto;
  height: 40px;
}

.logo svg path.logo-text {
  fill: #000;
}

.index .header .logo svg path.logo-text {
  fill: #fff;
}

.menu-trigger {
  padding-left: 60px;
  width: 100px;
  height: 30px;
  line-height: 30px;
  position: relative;
  color: red;
  text-transform: uppercase;
  transition: background 0.3s, color 0.5s;
  cursor: pointer;
  overflow: hidden;
}

.menu-trigger:focus {
  outline: none;
}

.menu-trigger span {
  display: block;
  width: 40px;
  height: 1px;
  position: absolute;
  top: 14px;
  left: 0;
  background: red;
  transition: background 0s 0.3s;
}

.menu-trigger span::before,
.menu-trigger span::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  background-color: #fff;
  transition-duration: 0.3s, 0.3s;
  transition-delay: 0.3s, 0s;
}

.menu-trigger span::before {
  top: -14px;
  transition-property: top, \transform;
}

.menu-trigger span::after {
  bottom: -15px;
  transition-property: bottom, \transform;
}

.navbar {
  flex-wrap: nowrap;
  padding: 0;
}

nav.navbar-collapse {
  display: flex;
  justify-content: flex-end;
}

nav.navbar-collapse .navbar-nav {
  flex-direction: row;
  justify-content: flex-end;
  margin: 0 4%;
  width: 80%;
}

nav .nav-item {
  margin: 0 2%;
}

nav .nav-link {
  color: #232323;
}

nav .nav-link-toggle::after {
  content: "\f107";
  display: inline-block;
  font-family: "Font Awesome 5 Free";
  font-size: 65%;
  font-weight: 900;
  margin-left: .5rem;
  transition: 0.3s ease;
}

nav .navbar-collapse {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #333;
  z-index: 0;
}

nav .navbar-collapse .navbar-nav {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

a.target-burger {
  display: none;
  margin-left: 1.875rem;
  width: 61px;
  height: 30px;
  position: relative;
  transition: .5s;
}

a.target-burger:hover {
  cursor: pointer;
  opacity: .45;
}

a.target-burger.toggled ul.buns li.bun {
  background-color: #f00d22;
  transform: rotate(45deg) translateZ(0);
}

a.target-burger.toggled ul.buns li.bun:last-child {
  transform: rotate(-45deg) translateZ(0);
}

a.target-burger ul.buns {
  width: 31px;
  height: 31px;
  list-style: none;
  margin-bottom: 0;
  padding: 0;
  position: absolute;
  top: 50%;
  left: 0;
  transition: transform 1s cubic-bezier(0.23, 1, 0.32, 1), color 1s cubic-bezier(0.23, 1, 0.32, 1);
  transform: translateZ(0);
  color: #fff;
}

a.target-burger ul.buns li.bun {
  width: 100%;
  height: 2px;
  background-color: #fff;
  margin-top: -.75px;
  transform: translateY(-3.75px) translateZ(0);
  transition: transform 1s cubic-bezier(0.23, 1, 0.32, 1), background-color 1s cubic-bezier(0.23, 1, 0.32, 1);
}

a.target-burger ul.buns li.bun:last-child {
  transform: translateY(3.75px) translateZ(0);
}

.header {
  padding: 20px 0;
  width: 100%;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.02);
  z-index: 200;
}

.index .header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.index .header a {
  color: #fff;
}

.index .header .call {
  color: #fff;
}

.index .header .call .btn {
  color: #fff;
  border-color: #fff;
}

.call {
  display: flex;
  flex-direction: column;
  text-align: right;
}

.phone {
  margin-bottom: 0.75rem;
  font-family: "Oswald", sans-serif;
  font-size: 1.3125rem;
  white-space: nowrap;
}

.phone span {
  color: #f00d22;
}

.breadcrumb {
  margin: 0;
  padding: 10px 0 0;
  background-color: transparent;
  border-radius: 0;
}

.breadcrumb-item {
  display: flex;
  align-items: center;
  font-size: 12px;
  line-height: 20px;
}

.breadcrumb-item a {
  color: #232323 !important;
}

.breadcrumb-item i.fa,
.breadcrumb-item i.fa::before {
  color: #f00d22;
  font-size: 16px;
  margin-right: 4px;
}

.breadcrumb-item:last-child {
  color: #f00d22;
}

.breadcrumb-item::before {
  position: relative;
  top: 3px;
  opacity: 0.6;
}

section {
  padding: 90px 0;
}

.header + section:not(.top):not(.jumbotron),
section.breadcrumb-nav + section,
section.breadcrumb-nav,
section.main {
  padding-top: 25px;
}

.header + section.jumbotron {
  padding-bottom: 0;
}

section.breadcrumb-nav {
  padding-bottom: 0;
}

.top {
  padding-bottom: 200px;
  height: 100vh;
  background: #f8f9fa url(../img/top.jpg) 50% 50% no-repeat;
  background-size: cover;
}

.index .top {
  padding-top: 140px;
}

.top .container,
.top .top-box {
  height: 100%;
}

.statistics {
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 40px 0;
  width: 100%;
}

.statistics ul {
  list-style-type: none;
}

.statistics ul li {
  min-height: 130px;
  background: url(../img/separator.png) 100% 50% no-repeat;
}

.statistics ul li:last-child {
  background: none;
}

.statistics ul li p {
  color: #fff;
}

.statistics ul li .num {
  font-size: 28px;
  font-weight: 700;
}

.pricies .price {
  font-size: 2.5rem;
  font-family: "Oswald", sans-serif;
  text-align: right;
  white-space: nowrap;
}

.pricies .price span {
  font-size: 14px;
}

.rise {
  padding-top: 100px;
}

.rise #app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.rise #app .funnels {
  height: 560px;
  margin-top: 16px;
}

.rise #app .funnel:not(.svg-funnel-js--vertical) {
  transition: transform 0.3s ease;
  transform: translateY(60px);
}

.rise #app .appear-enter-active,
.rise #app .appear-leave-active {
  transition: all .7s ease-in-out;
}

.rise #app .appear-enter-to,
.rise #app .appear-leave {
  max-width: 100%;
  max-height: 100%;
  opacity: 1;
}

.rise #app .appear-enter,
.rise #app .appear-leave-to {
  max-width: 0;
  max-height: 0;
  opacity: 0;
}

.rise #app .fade-enter-active,
.rise #app .fade-leave-active {
  transition: all .3s ease;
}

.rise #app .fade-enter-to,
.rise #app .fade-leave {
  opacity: 1;
}

.rise #app .fade-enter,
.rise #app .fade-leave-to {
  opacity: 0;
}

.rise-chart {
  position: relative;
  margin: 0 auto 60px;
  height: 300px;
}

.rise-point {
  position: absolute;
  margin-top: -150px;
  opacity: 0;
}

.rise-point .btn {
  display: inline-block;
  margin-bottom: 6px;
  padding: 4px 6px;
}

.rise-point_title {
  margin-bottom: 6px;
  font-weight: bold;
  line-height: 1rem;
}

.rise-point p {
  font-size: 12px;
}

.jumbotron {
  position: relative;
  color: #fff;
  overflow: hidden;
}

.jumbotron * {
  color: #fff;
}

.jumbotron.red {
  background: #de2025;
  background: radial-gradient(circle, #de2025 0%, #7e2622 100%);
}

.jumbotron.blue {
  background: #0274ff;
  background: radial-gradient(circle, #0274ff 0%, #074897 100%);
}

.jumbotron canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.projects {
  overflow: hidden;
}

.projects .grid-container {
  margin: 0 auto;
  max-width: 1160px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas: "b1 b1 b1 b2 b2" "b3 b3 b4 b4 b4";
  grid-column-gap: 30px;
  grid-row-gap: 30px;
}

.projects .grid-container .projects-block {
  position: relative;
  padding: 30px;
  border-radius: 8px;
}

.projects .grid-container .projects-block * {
  color: #fff;
}

.projects .grid-container .projects-block .h3 {
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.projects .grid-container figure {
  position: absolute;
}

.projects .grid-container figure img {
  width: 100%;
  height: auto;
}

.projects .grid-container .b1 {
  grid-area: b1;
  background-color: #a7cdce;
}

.projects .grid-container .b1 figure {
  right: -160px;
  bottom: -10px;
  height: 90%;
}

.projects .grid-container .b2 {
  grid-area: b2;
  background-color: #0174ff;
}

.projects .grid-container .b2 figure {
  right: -80px;
  bottom: 0px;
  width: 70%;
  transform: rotate(-2deg);
}

.projects .grid-container .b3 {
  grid-area: b3;
  background-color: #0f99fa;
  padding-bottom: 260px;
}

.projects .grid-container .b3 figure {
  left: -5%;
  bottom: -50px;
  width: 110%;
}

.projects .grid-container .b4 {
  grid-area: b4;
  background-color: #e1bd6f;
}

.projects .grid-container .b4 figure {
  right: -160px;
  bottom: -50px;
  width: 70%;
}

.work .case {
  padding: 30px;
  border-radius: 8px;
  background: url(../img/case-img.jpg) 50% 50% no-repeat;
  background-size: cover;
  overflow: hidden;
}

.work .case * {
  color: #fff;
}

.work .case h2 {
  font-size: 2.5rem;
}

.work .case .tag {
  border-color: #fff;
}

.contacts .contact {
  background-image: url(../img/pattern.png), linear-gradient(225deg, #ff263c 0%, #f00d22 100%);
  background-position: 0 4%;
  background-repeat: repeat;
}

.contacts .contact * {
  color: #fff;
}

.contacts .map {
  min-height: 400px;
}

.contacts .svg-map path {
  fill: #fff;
}

.tags {
  margin: 16px 0 24px;
}

.tags .tag {
  margin: 5px;
  padding: .4rem .6rem;
}

.triangles {
  position: relative;
  overflow: hidden;
}

.triangles.red svg .fil12 {
  fill: #ED1C23;
  fill-rule: nonzero;
  fill-opacity: 0.4;
}

.triangles.red svg .el-light {
  fill: rgba(228, 178, 180, 0.27);
  fill-rule: nonzero;
  fill-opacity: 0.4;
}

.triangles figure {
  position: absolute;
  top: 0;
  width: 40%;
  height: 100%;
  opacity: .8;
}

.triangles figure svg {
  width: auto;
  height: 100%;
}

.red .triangles figure svg .fil12 {
  fill: #ED1C23;
  fill-rule: nonzero;
  fill-opacity: 0.4;
}

.red .triangles figure svg .el-light {
  fill: rgba(228, 178, 180, 0.27);
  fill-rule: nonzero;
  fill-opacity: 0.4;
}

.blue .triangles figure svg .fil12 {
  fill: #0274ff;
  fill-rule: nonzero;
  fill-opacity: 0.4;
}

.blue .triangles figure svg .el-light {
  fill: rgba(2, 116, 255, 0.27);
  fill-rule: nonzero;
  fill-opacity: 0.4;
}

.triangles figure.triangle_l {
  left: 0;
}

.triangles figure.triangle_r {
  right: 0;
  transform: rotate(180deg);
}

.footer {
  padding: 20px 0;
  width: 100%;
  background-color: #fdfdfd;
  z-index: 200;
}

@media (min-width: 1201px) {
  h1,
  .h1 {
    margin-bottom: 3rem;
    font-size: 3rem;
  }

  h1 + .lead,
  .h1 + .lead {
    margin-top: -2.4rem;
  }

  h2,
  .h2 {
    margin-bottom: 2.5rem;
    font-size: 1.5rem;
    font-weight: 600;
  }

  h3,
  .h3 {
    margin-bottom: 2.5rem;
    font-size: 1.3125rem;
    font-weight: 600;
  }

  h4,
  .h4 {
    margin-bottom: 2rem;
    font-size: 1.125rem;
    font-weight: 600;
  }

  h5,
  .h5 {
    margin-bottom: 2rem;
    font-size: 1rem;
    font-weight: 600;
  }

  h6,
  .h6 {
    margin-bottom: 2rem;
    font-size: 1rem;
  }

  .section-title {
    margin-bottom: 3.75rem;
  }
}

@media (max-width: 1400px) {
  html {
    font-size: 14px;
  }
}

@media (max-width: 1020px) {
  nav.navbar-collapse {
    display: none;
  }

  a.target-burger {
    display: block;
  }
}

@media (max-width: 860px) {
  .triangles figure.triangle_l {
    left: -180px;
  }

  .triangles figure.triangle_r {
    right: -180px;
  }
}

@media (max-width: 769px) {
  .projects .grid-container .b2 figure {
    right: -46px;
    bottom: -50px;
  }

  .projects .grid-container .b2 figure img {
    height: 100%;
  }

  .projects .grid-container .b3 {
    padding-bottom: 160px;
  }

  .projects .grid-container .b3 figure {
    left: 1%;
    width: 98%;
  }

  .projects .grid-container .b4 figure {
    right: -100px;
    bottom: -50px;
    width: 80%;
  }

  .projects .grid-container .b4 figure img {
    height: 100%;
  }

  html {
    font-size: 14px;
  }

  body {
    font-size: 1rem;
  }

  section,
  .rise {
    padding: 40px 0;
  }

  h1,
  .h1 {
    font-size: 2.6rem;
  }
}

@media (max-width: 768px) {
  .pricies .price-block:not(:last-child) {
    margin-bottom: 3rem;
    border-bottom: 1px solid #d8d8d8;
  }

  .projects .grid-container {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto auto;
    grid-template-areas: 'b1' 'b2' 'b3' 'b4';
    grid-row-gap: 60px;
  }
}

@media (max-width: 767px), (max-height: 760px) {
  .top {
    padding-top: 120px;
    padding-bottom: 0;
  }

  .statistics {
    position: relative;
    bottom: auto;
    background-color: #f8f9fa;
  }

  .statistics ul li p {
    color: #232323;
  }
}

@media (max-width: 600px) {
  .index .top {
    padding-top: 100px;
  }
}

@media (max-width: 500px) {
  .header {
    padding: 8px 0;
  }

  .header .logo img {
    height: 40px;
  }
}

@media (max-width: 500px), (orientation: landscape) and (max-height: 400px) {
  html {
    font-size: 12px;
  }

  h1,
  .h1 {
    margin-bottom: 2.6rem;
    font-size: 2.3rem;
  }
}

@media (max-width: 460px) {
  .logo svg {
    height: 30px;
  }
}

@media (orientation: landscape) and (max-height: 670px) {
  .top {
    height: auto;
  }
}